const Navigation = ({onRouteChange, isSignedIn}) => {

		if (isSignedIn) {
			return (
				<nav className='Navigation-div flex justify-end'>
					<p onClick={() => onRouteChange('signOut')}  className='navigation f3 link underline pa3 pointer '>{/* Sign Out */}</p>
				</nav>
				);
		} else {
			return(
				<nav className='Navigation-div flex justify-end'>
					<p onClick={() => onRouteChange('signIn')}  className='navigation f3 link underline pa3 pointer '>{/* Sign In */}</p>
					<p onClick={() => onRouteChange('Register')}  className='navigation f3 link underline pa3 pointer '>{/* Register */}</p>
				</nav>
				);
		}
		


	
}

export default Navigation