import './ImageLinkForm.css';


const ImageLinkForm = ({ onInputChange, onButtonSubmit }) => {
	return (
		<div className='imageLinkForm center'>
			<div className="center flex justify-center f3 w-70">
				<p className='app-discription pa1 br4  shadow-1'>
					{'This Magic Brain will detect faces in your pictures. Give it a try.'}
				</p>
			</div>
			<div>
				<div className='search-bar shadow-1 pa3 br4 ma1 w-70 center'>
					<input 
						className='f4 pa1 w-70 center br3' 
						type='text' 
						onChange={onInputChange}/>
					<button 
						className='tc f3 w-30 link ph3 br3 dib white' 
						onClick={onButtonSubmit}
					>Detect</button>
					<div className="searchHint">
						<p> Input an image link ending in a valid image extension (i.e. ".jpg .png ")</p>
					</div>
				</div>
			</div>
		</div>
	);

	
}

export default ImageLinkForm