import Tilt from 'react-tilt'
import './Logo.css';

const Logo = () => {
	return (
		<div className='logo ma4 mt0 flex justify-center center'>
			<Tilt className="Tilt br4 " options={{ max : 40 }} style={{ height: 150, width: 150 }} >
				<div className="Tilt-inner pa3" style={{ height: 150, width: 150 }}> 
					<Tilt className="Tilt-secondary br4 center mt2 " options={{ max : 60 }} style={{ height: 100, width: 100 }} >
						<div className="Tilt-inner" style={{ height: 100, width: 100 }}> 
							<img  className='brain-logo' alt='Logo of a brain'src="https://img.icons8.com/carbon-copy/100/000000/brain.png"/> 
						</div>
					</Tilt>
				</div>

			</Tilt>
		</div>
	);
	
}

export default Logo