import './FaceRecognition.css';
import Tilt from 'react-tilt'



const FaceRecognition = ({imageURL, box, imageHeight}) => {

let boxes=[];
let i =0;


for (i; i < box.length ; i++) {
	boxes[i] = <div 
					key={i}
					className='bounding-box' 
					style={{top:box[i].topRow, right:box[i].rightCol, bottom:box[i].bottomRow, left:box[i].leftCol}}>
				</div>	
}

		return (
			<div className="dib">
				<Tilt className="Tilt-secondary br4 center" options={{ max : 25 }} style={{ width: 500 }} >
					<div className="Tilt-inner" style={{ height: 100, width: 500 }}> 
						<div className='FaceRecognition center'>
							<img id='inputImage' className='center br4 shadow-1'  src={imageURL} alt='' width='500px' height='auto'/>
							{boxes}
							
						</div>						
					</div>
			
				</Tilt>
				<div className="buffer">
					
				</div>
			</div>
			
		);
}

export default FaceRecognition